// Contentinfo
// ===========

[data-region='contentinfo'] {
  @include font-family('boilerplate');
  @include font-size('smaller', 0.75);
  border-top: 2px dotted color('border');
  clear: both;
  color: color('gray');
  margin: 0 auto;
  max-width: size('page');
  padding: size('half-shim') size('shim') size('spacer');
  text-transform: uppercase;
}

.social {
  a + a {
    @include before(' | ') {
      color: color('gray');
    }
  }
}
